<template>
  <div>
  <div class="my-prize-one">
    <p class="p-title">
      集爱数字卡片
    </p>
    <carousel-3d ref="carousel" :inverse-scaling="1500"  :height="260" :width="244.4" :space="450" >
      <slide  v-for="(slide, i) in slides" :index="i" :key="i">
        <span class="title">{{slide.a}}</span>
        <p></p>
      </slide>
    </carousel-3d>
  </div>
    <div class="my-price-two">
      <div class="s-card" v-for="(item,i) in scItems" @click="$refs.carousel.goSlide(i-1)">
        <span class="card-badge" v-if="item.b">x1</span>
      </div>
    </div>
  </div>
</template>

<script>
  import { Carousel3d, Slide } from 'vue-carousel-3d';
export default {
  name: 'myPrizeOne',
  data () {
    return {
      slides:[
          {a:1},
          {a:2},
          {a:3}
          ],
      scItems:[
        {a:1,b:1},
        {a:1},
        {a:1},
        {a:1}

      ]
      }

  },
  components:{
    Carousel3d,
    Slide
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
 @import "my-prize-one.scss";
 @import "my-prize-two.scss";
</style>




